body {
  margin: 0;
  font-family: Beatrice,GT America,Helvetica,Verdana,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.pre-header {
  height: 30px;
}

.app-header {

}

.search-dropdown-wrapper {
  position: absolute;
  width: 100%;
  border: 1px solid #eaeaea;
  min-height: 50px;
  border-radius: 0 0 10px 10px;
  z-index: 100;
  background-color: white;
  color: black;
}

.nav-text {
  font-size: 12px;
  font-family: Beatrice,GT America,Helvetica,Verdana,sans-serif;
  font-weight: bold;
}

.nav-component {
  display: flex;
  align-items: center;
  color: #eb1700;
  font-family: Beatrice,GT America,Helvetica,Verdana,sans-serif;
  font-size: 12px;
  letter-spacing: -.15px;
  line-height: 1;
}

.nav-component-space {
  width: 15px;
}

.nav-component-fat-space {
  width: 400px; 
}

.nav-search-wrapper {
  position: relative;
}

.nav-search-logo {
  position: absolute;
  top: 18px;
  left: 10px;
}

.nav-search-input {
  border: 1px solid #eaeaea;
  border-radius: 4px;
  height: inherit;
  padding: 0 40px;
  height: 50px;
  width: 300px;
}

.search-logo {
  height: 18px;
  width: 18px;
}

.logo-wrapper {
  padding-top: 4px;
  position: relative;
  display: flex;
}

.the-g {
  z-index: 10;
  color: white;
  position: relative;
  bottom: -3px;
  left: 13px;
  font-size: 22px;
  font-weight: bold;
}

.main-nav-wrapper {
  border-bottom: 1px solid #eaeaea;
  border-top: 1px solid #eaeaea;
  display: flex;
  justify-content: center;
}

.main-nav {
  padding:  10px 0 10px 0;
  display: flex;
  justify-content: center;
  margin: 5px;
}

.main-nav-component {

}

.red-icon {
  color: #eb1700;
}

.profile-image-wrapper {
  width: 42px;
  height: 42px;
  overflow: hidden;
  border-radius: 20px;
}

.profile-image {
  width: 100%;
  height: 100%;
}

.main-wrapper {
  display: flex;
  justify-content: center;
}

.date-choice-wrapper {
  
}

.date-picker-dropdown {
  background-color: white;
  border: 0px;
  color: #eb1700;
}

.search-result-wrapper {
  padding: 5px;
  min-height: 30px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
  z-index: 200;
}

.search-result-image {
  width: 60px;
}

.search-result-text {
  font-size: 18px;

}

.detail-page-wrapper {
  display: flex;
  max-width: 80%;
}

.detail-column {
  padding: 50px;
}

.detail-column-one {
  max-width: 45%;
}

.detail-column-one {
}

.detail-image {
  max-width: 475px;
  border-radius: 14px;
}

.detail-nav-wrapper {
  padding: 8px;
  border: 1px solid #eaeaea;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
}

.inventory-list-wrapper {
  padding: 20px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.inventory-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #336dde;
  color: #fff;
  padding: 10px;
  min-width: 180px;
  ming-height: 40px;
  margin-bottom: 8px;
  border-radius:  10px;
  cursor: pointer;
}

.inventory-wrapper:hover {
  background-color: #4829B3
}